import React from "react";
import { Box, Typography } from "@mui/material";
import Icon from "../../src/assets/Gift/GiftIcon.png"
import Gift1  from "../../src/assets/Gift/Gift-1.png";
import Gift2  from "../../src/assets/Gift/Gift-Hero.webp";
export default function GiftTrip() {
  const features = [
    {
      title: "Memories Over Material Things",
      text: "Unlike objects that gather dust, experiences last a lifetime. A road trip, an expedition, or a cultural deep dive creates stories to cherish forever.",
    },
    {
      title: "Perfect for All Occasions",
      text: "Whether its a birthday, anniversary, graduation, or just because  you can never go wrong with an adventure!",
    },
    {
      title: "Tailored Adventures with Embarq",
      text: "Imagine the excitement of an offbeat road trip through uncharted territories or the adrenaline rush of an expedition into the wild. Embarq crafts journeys that redefine the way we travel!",
    },
    {
      title: "Transformation Through Travel",
      text: "Its more than just a vacation; its an opportunity for self-discovery, new perspectives, and unforgettable moments.",
    },
    {
      title: "A Gift of Escape",
      text: "Give your loved ones a break from routine and a chance to explore breathtaking landscapes, hidden gems, and thrilling new cultures.",
    },
  ];
  const FeatureCard = ({ item }) => (
    <Box
      sx={{
        backgroundColor: "#F1F1F1",
        borderRadius: "16px",
        p: 2.5,
        display: "flex",
        gap: 1.5,
      }}
    >
     <Box
      component="img"
      src={Icon}
      alt="feature icon"
      sx={{
        width: 50,
        height: 50,
        objectFit: "contain",
        mt: "7px",
      }}
    />
      <Box>
        <Typography sx={{ fontSize: 14, fontWeight: 600, mb: 0.5 }}>
          {item.title}
        </Typography>
        <Typography sx={{ fontSize: 13, color: "#666", lineHeight: 1.6 }}>
          {item.text}
        </Typography>
      </Box>
    </Box>
  );

  return (
    <> {/* ================= HERO SECTION ================= */}
    <Box
  sx={{
    width: "100%",
    height: { xs: "60vh", md: "100vh" },
    backgroundImage: `url(${Gift2})`,
    backgroundSize: "cover",
    backgroundPosition: "center",
    position: "relative",
    display: "flex",
    alignItems: "center",
  }}
>
  {/* Overlay */}
  <Box
    sx={{
      position: "absolute",
      inset: 0,
      backgroundColor: "#0E0E0E1F",
    }}
  />

  {/* Left Aligned Content Wrapper */}
  <Box
    sx={{
      position: "relative",
      maxWidth: "1200px",
      width: "100%",
      mx: "auto",
      px: { xs: 3, md: 6 },
    }}
  >
    <Typography
      variant="h2"
      sx={{
        color: "#fff",
        fontFamily: "'Fraunces', sans-serif",
        fontWeight: 700,
        textAlign: "left",
        fontSize: { xs: "36px", md: "64px" },
        letterSpacing: "1px",
      }}
    >
      Gift a Trip
    </Typography>
  </Box>
</Box>

    <Box sx={{ backgroundColor: "#FFFFFF", py: 1 ,}}>
        
      <Box
        sx={{
            mt: 5,          maxWidth: "1300px",
          mx: "auto",
          px: 3,
          display: "grid",
          gridTemplateColumns: {
            xs: "1fr",
            md: "1.2fr 1fr",
          },
          gap: {
            xs: 4,
            md: 6,
          },
          alignItems: "start",
        }}
      >
        {/* LEFT */}
        <Box>
          <Typography variant="h5" sx={{ fontWeight: 700, mb: 2 ,fontFamily: "'Roboto Flex', sans-serif",}}>
            Why Gift a Trip?
          </Typography>

          <Typography
            sx={{
              fontSize: 14,
              color: "#555",
              mb: 4,
              lineHeight: 1.7,
              maxWidth: "520px",
              fontFamily: "'Roboto Flex', sans-serif",
            }}
          >
            A trip isnt just a gift  its an unforgettable experience wrapped in adventure and discovery! When youre struggling to find the perfect present for someone who has it all, gifting travel is the ultimate way to surprise and delight.
            <br/>
            <br/>
            Heres why a travel experience makes the perfect gift:
          </Typography>

          <Box sx={{ display: "flex", flexDirection: "column", gap: 3 }}>
            {features.slice(0, 3).map((item, i) => (
              <FeatureCard key={i} item={item} />
            ))}
          </Box>

          <Typography sx={{ mt: 4, fontSize: 13, color: "#666" ,fontFamily: "'Roboto Flex', sans-serif",}}>
           With Embarq, youre not just gifting a trip, youre gifting freedom, exploration, and the magic of the unknown. Ready to surprise someone with the adventure of a lifetime?
          </Typography>
        </Box>

        {/* RIGHT */}
        <Box sx={{ display: "flex", flexDirection: "column", gap: 4 }}>
          {/* IMAGE GRID */}
          <Box
            sx={{
              p: 1.5,
              borderRadius: "24px",
              display: "grid",
              gridTemplateColumns: {
                xs: "1fr 1fr",
                sm: "1fr 1fr",
                md: "1.2fr 1fr",
              },
              gridTemplateRows: {
                xs: "180px 120px ",
                sm: "200px 120px",
                md: "220px 120px",
              },
              gap: 2,
            }}
          >
            {/* BIG */}
            <Box
              component="img"
              src={Gift1}
              sx={{
                width: "100%",
                height: "100%",
                objectFit: "cover",
                borderRadius: "18px",
                gridRow: {
                  xs: "1 / span 1",
                  sm: "1 / span 2",
                  md: "1 / span 2",
                },
              }}
            />

            {/* TOP RIGHT */}
            <Box
              component="img"
              src={Gift1}
              sx={{
                width: "100%",
                height: "100%",
                objectFit: "cover",
                borderRadius: "18px",
                transform: {
                  xs: "translateY(0px)",
                  sm: "translateY(-14px)",
                  md: "translateY(-20px)",
                },
              }}
            />

            {/* BOTTOM RIGHT */}
            <Box
              component="img"
              src={Gift1}
              sx={{
                width: "95%",
                height: "115%",
                objectFit: "cover",
                borderRadius: "18px",
                 transform: {
                  xs: "translateY(0px)",
                  sm: "translateY(14px)",
                  md: "translateY(-20px)",
                },
              }}
            />
          </Box>

          <Box sx={{ display: "flex", flexDirection: "column", gap: 3 ,fontFamily: "'Roboto Flex', sans-serif",}}>
            {features.slice(3).map((item, i) => (
              <FeatureCard key={i} item={item} />
            ))}
          </Box>
        </Box>
      </Box>
    </Box>
    </>
  );
}




